___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.content {
    display: grid;
    grid-template-rows: 100px 1fr;
    height: 100vh;
    width: 100vw;
}

header {
    background-color: rgb(143, 237, 143)
}


header, ul{
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

li {
    list-style: none;
    background-color: rgb(192, 244, 192);
    padding: 5px;
    border-radius: 8px;
}

.main {
    display: flex;
    flex-direction: column;
    align-content: center;
    margin-top: 10px;
}

.main div{
    display: flex;
    flex-direction: column;
    align-items:center ;
    background-color: bisque;
    padding: 10px;
    margin-top: 10px;
    margin-left:50px;
    margin-right:50px;
    border-radius: 16px;
}
